/*=======================================
    GENERAL STYLES
==================================================*/
body {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
}

.text-center {
  text-align: center;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Open Sans", sans-serif;
}
.alg-right-pad {
  float: right;
  padding-right: 20px;
}
a:hover {
  text-decoration: none;
}

/*=======================================
  NAVBAR ADJUSTMENTS
==================================================*/
.navbar-brand {
  font-size: 25px;
  padding: 15px 80px 15px 60px;
}
.navbar-default .navbar-brand {
  color: #000000;
}
.navbar-default {
  background-color: #000000;
  border-color: #000000;
}
.navbar {
  border-radius: 0px;
}
.navbar-default .navbar-nav > li > a {
  color: #fff;
}
.navbar-default .navbar-nav > li > a:hover {
  color: #fff;
}

.navbar-default .navbar-brand {
  color: #ffffff;
}
.navbar-default .navbar-brand:hover {
  color: #ffffff;
}
.navbar-default .navbar-nav > .open > a,
.navbar-default .navbar-nav > .open > a:hover,
.navbar-default .navbar-nav > .open > a:focus {
  color: #050505;
  background-color: #ffffff;
}
.navbar-default .navbar-nav .open .dropdown-menu > li > a {
  color: #000;
}
.navbar-default .navbar-nav .open .dropdown-menu > li > a:hover {
  color: #000;
}
/*=======================================
  BOOTSTRAP ADJUSTMENTS
==================================================*/
.row {
  margin-bottom: 10px;
}
.btn {
  margin-bottom: 5px;
}
/*=======================================
  INDEX PAGE
==================================================*/

.box-border {
  border: 1px solid #e1e1e1;
}

.product-box h3 > a,
.product-box h3 > a:hover,
.product-box h3 > a:focus {
  color: #000;
  text-decoration: none;
}

.offer-box {
  background-color: #6a75f0;
  border: 1px solid #096fc0;
  color: #fff;
  font-weight: 900;
  font-size: 16px;
}
.offer-box a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
}
.offer-box a:hover {
  color: #000;
}
.list-group a {
  font-size: 14px;
  font-weight: 900;
  color: #808080;
}
.list-group a:hover {
  color: #000;
  text-decoration: none;
}
.offer-colors {
  background-color: #b05fdd;
  border: 1px solid #a228f3;
}

.download-app-box {
  background-color: #069e98;
  border: 1px solid #0faa8b;
  padding: 30px;
  line-height: 40px;
  font-size: 25px;
  font-weight: 900;
  color: #fff;
}
/*=======================================
  SLIDER main-style.css Changes
==================================================*/
.mi-slider {
  margin-top: 0px;
}

.offer-text {
  position: absolute;
  color: #fff;
  padding: 14px 16px 12px 16px;
  margin-top: 10px;
  background-color: #18b424;
}
.offer-text2 {
  position: absolute;
  color: #fff;
  padding: 14px 16px 12px 16px;
  margin-top: 10px;
  background-color: #eb0429;
}
/*=======================================
  FOOTER SECTION
==================================================*/
.footer-box {
  background-color: #f5f5f5;
  padding: 20px 50px 20px 50px;
}
.small-box {
  font-size: 10px;
}

.footer-box a {
  color: #808080;
  text-decoration: none;
}
.footer-box a:hover {
  color: #000;
}
.pad-botom {
  padding-bottom: 30px;
}
.footer-box p {
  padding-top: 15px;
  padding-bottom: 15px;
  line-height: 25px;
}
.social-box a {
  color: #333436;
  text-decoration: none;
}

.end-box {
  padding: 20px;
  color: #fff;
  background-color: #000;
  font-size: 14px;
}

.product-box {
  display: block;
  padding: 4px;
  margin-bottom: 20px;
  line-height: 1.42857143;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.style_featured {
  padding: 20px 0;
  text-align: center;
}
.style_featured > div > div {
  padding: 10px;
  border: 1px solid transparent;
  border-radius: 4px;
  transition: 0.5s;
}
.style_featured > div:hover > div {
  margin-top: +19px;
  border: 1px solid rgb(153, 200, 250);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 9px 9px 9px;
  background: rgba(153, 200, 250, 0.1);
  transition: 0.99s;
}

